import { D1ReadEndpoint } from 'chanfana';
import { HandleArgs } from '../../types';
import { CustomerModel } from './base';

export class CustomerRead extends D1ReadEndpoint<HandleArgs> {
  schema = {
    tags: ["Customers"],
    summary: "Get customer by ID",
    request: {
      params: {
        id: {
          in: "path",
          name: "id",
          required: true,
          schema: { type: "integer" },
        },
      },
    },
  } as const;
  _meta = {
    model: CustomerModel,
  };
}